/* 
	We do not recommend changing the parameters
	to keep the look and feel consistent
*/

#MeteorToys_status {
	height: 48px; /* maximum 600px */
	width: 48px;  /* maximum 600px */
	transition: 0s !important;
	box-shadow: inset 0 0 0 30px rgba(0,0,0,.34);
}

#MeteorToys_status:hover {
	box-shadow: inset 0 0 0 30px rgba(0,0,0,.86);
}

#MeteorToys_status .MeteorToys_icon {
	
}

.MeteorToys_icon_online {
	background-image: url('./icon_online.png');
}

.MeteorToys_icon_offline {
	background-image: url('./icon_offline.png');
}
.MeteorToys_icon_connecting {
	background-image: url('./icon_connecting.png');
}